import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as actions from './redux/actions';
import { Shellbar, Menu } from 'fundamental-react';
export class Header extends Component {
  static propTypes = {
    common: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
  };

  render() {
    return (
      <Shellbar
        actions={[
          {
            callback: function w() {},
            glyph: 'settings',
            label: 'Settings',
            menu: (
              <Menu>
                <Menu.List>
                  <Menu.Item url="/fiori">Option 1</Menu.Item>
                  <Menu.Item url="/">Option 2</Menu.Item>
                  <Menu.Item url="/">Option 3</Menu.Item>
                </Menu.List>
              </Menu>
            ),
            notificationCount: 5,
          },
        ]}
        copilot
        logoSAP
        notifications={{
          callback: function w() {},
          glyph: 'bell',
          label: 'Notifications',
          noNotificationsBody: (
            <Menu key="notifications2">
              <Menu.List key="notifications00">
                <Menu.Item key="Notification4">There are no notifications</Menu.Item>
              </Menu.List>
            </Menu>
          ),
          notificationCount: 2,
          notificationsBody: (
            <Menu key="notifications1">
              <Menu.List key="notifications11">
                <Menu.Item key="Notification1" url="/">
                  Notification 1
                </Menu.Item>
                <Menu.Item key="Notification2" url="/">
                  Notification 2
                </Menu.Item>
                <Menu.Item key="Notification3" url="/">
                  Notification 3
                </Menu.Item>
              </Menu.List>
            </Menu>
          ),
        }}
        productMenu={[
          {
            callback: evt => {
              this.props.history.push('/fiori/tables');
            },
            name: 'Fiori3 Components',
          },
          {
            callback: function w() {},
            name: 'Application B',
          },
          {
            callback: function w() {},
            name: 'Application C',
          },
          {
            callback: function w() {},
            name: 'Application D',
          },
        ]}
        productSwitcher={{
          callback: function w() {},
          glyph: 'grid',
          label: 'Product Switcher',
        }}
        productSwitcherList={[
          {
            callback: function w() {},
            glyph: 'home',
            image:
              'data:image/png;base64,',
            title: 'Fiori Home',
          },
          {
            callback: function w() {},
            glyph: 'cloud',
            image:
              'data:image/png;base64,',
            title: 'S/4 HANA Cloud',
          },
          {
            callback: function w() {},
            glyph: 'business-objects-experience',
            image:
              'data:image/png;base64,',
            title: 'Analytics Cloud',
          },
          {
            callback: function w() {},
            glyph: 'activate',
            image:
              'data:image/png;base64,',
            title: 'Ariba',
          },
          {
            callback: function w() {},
            glyph: 'message-success',
            image:
              'data:image/png;base64,',
            title: 'SuccessFactors',
          },
          {
            callback: function w() {},
            glyph: 'retail-store',
            image:
              'data:image/png;base64,',
            title: 'Commerce Cloud',
          },
          {
            callback: function w() {},
            glyph: 'customer-view',
            image:
              'data:image/png;base64,',
            title: 'Gigya',
          },
          {
            callback: function w() {},
            glyph: 'globe',
            image:
              'data:image/png;base64,',
            title: 'Callidus Cloud',
          },
          {
            callback: function w() {},
            glyph: 'work-history',
            image:
              'data:image/png;base64,',
            title: 'Fieldglass',
          },
          {
            callback: function w() {},
            glyph: 'area-chart',
            image:
              'data:image/png;base64,',
            title: 'Concur',
          },
          {
            callback: function w() {},
            glyph: 'customer-view',
            image:
              'data:image/png;base64,',
            title: 'Cloud for Customer',
          },
          {
            callback: function w() {},
            glyph: 'customer',
            image:
              'data:image/png;base64,',
            title: 'Cloud Portal',
          },
        ]}
        productTitle="Finance SaaS"
        profile={{
          image: 'https://sap.github.io/fundamental-react/static/media/headshot-male.10d4e22e.jpg',
          userName: 'John Snow',
        }}
        profileMenu={[
          {
            callback: function w() {},
            glyph: 'action-settings',
            name: 'Settings',
            size: 's',
          },
          {
            callback: function w() {},
            glyph: 'log',
            name: 'Sign Out',
            size: 's',
          },
        ]}
        searchInput={{
          callback: function w() {},
          glyph: 'search',
          label: 'Search',
          onSearch: function w() {},
          placeholder: 'Enter a term',
          searchList: [
            {
              callback: function w() {},
              text: 'apple',
            },
            {
              callback: function w() {},
              text: 'apricot',
            },
            {
              callback: function w() {},
              text: 'acai',
            },
            {
              callback: function w() {},
              text: 'banana',
            },
            {
              callback: function w() {},
              text: 'berry',
            },
            {
              callback: function w() {},
              text: 'blueberry',
            },
            {
              callback: function w() {},
              text: 'blackberry',
            },
            {
              callback: function w() {},
              text: 'cranberry',
            },
            {
              callback: function w() {},
              text: 'conkerberry',
            },
            {
              callback: function w() {},
              text: 'calabash',
            },
            {
              callback: function w() {},
              text: 'clementines',
            },
            {
              callback: function w() {},
              text: 'kiwi',
            },
            {
              callback: function w() {},
              text: 'orange',
            },
          ],
        }}
        subtitle="Fiori3 React"
      />
    );
  }
}

/* istanbul ignore next */
function mapStateToProps(state) {
  return {
    common: state.common,
  };
}

/* istanbul ignore next */
function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({ ...actions }, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Header);
